<template>
  <v-container>
    <div id="gameMap"></div>
    <div class="demo-intro">
      <h3>DEMO 说明</h3>
      <p>每隔5秒随机出现宝物(圆点代表),25秒后自动消失,点击圆点进入Game</p>
    </div>
  </v-container>
</template>

<script>
import { cloneDeep } from 'lodash'
import {
  colors,
  topographies,
  coordinates,
  getRandomTopography,
  AllMapIds,
  getMapText
} from './maps-helper'
const w = 1200
const h = 800
const size = 200
const appearInterval = 5
const disappearSeconds = 25
const shakeDuration = 2.5

const circleD = 10
const circleDMax = 18
const circleRMin = 1
const tooltipId = 'ToolTipDiv'

const tipWidth = 170
const tipHightMax = 80
const tipDiffH = 4
const tipDiffW = 6

const tipPaddingX = 6
const tipPaddingY = 4

import * as d3 from 'd3'
export default {
  name: 'IndexMap',
  components: {},
  data() {
    return {
      allMapIds: AllMapIds,
      gameRegions: [],
      appearMids: []
    }
  },
  computed: {},
  methods: {
    createSvgMap() {
      let prevous
      const svg = d3
        .select('#gameMap')
        .append('svg')
        .attr('width', w)
        .attr('height', h)
        .attr('id', 'game-v')

      const tooltip = d3
        .select('#gameMap')
        .append('div')
        .attr('id', tooltipId)
        .attr('class', 'game-tooltip')
        .style('opacity', 0)

      let currentMaps = []

      for (let i = 0; i < coordinates.length; i++) {
        const fillColor = getRandomTopography(prevous)

        const mapText = getMapText(fillColor)
        prevous = fillColor
        const mProps = coordinates[i]
        const { mid, x, y } = mProps

        const gameRegion = {
          ...mProps,
          type: mapText,
          bgColor: fillColor,
          name: `${mapText}#${mid}`
        }

        svg
          .append('rect')
          .attr('id', mid)
          .attr('x', x)
          .attr('y', y)
          .attr('width', size)
          .attr('height', size)
          .style('fill', fillColor)

        // drawer boder
        svg
          .append('line')
          .attr('x1', x)
          .attr('y1', y)
          .attr('x2', x + size)
          .attr('y2', y)
          .style('stroke', '#6495ED')
          .style('stroke-width', '1px')

        svg
          .append('line')
          .attr('x1', x + size)
          .attr('y1', y)
          .attr('x2', x + size)
          .attr('y2', y + size)
          .style('stroke', '#6495ED')
          .style('stroke-width', '1px')

        // draw text
        svg
          .append('text')
          .text(mapText)
          .attr('x', x)
          .attr('y', y + size)
          .attr('text-anchor', 'start')
          .attr('dx', '1em')
          .attr('dy', '-.75em')

        // add to virtraul dom
        currentMaps.push(gameRegion)
      }

      this.gameRegions = currentMaps

      // start random appearPoint
      this.timer = setInterval(() => {
        this.createCircle(tooltip)
      }, appearInterval * 1000)
    },
    getRandomMapId() {
      const mapIds = this.allMapIds

      const r = d3.randomInt(mapIds.length)

      return mapIds[r()]
    },
    getRandomCoordnates() {
      const _gameRegions = this.gameRegions
      const rangeCircle = _getRandomC()
      return rangeCircle

      function _getRandomC() {
        let rangeX = Math.random() * w
        let rangeY = Math.random() * h

        let mapInfo = null

        while (!mapInfo) {
          rangeX = Math.random() * w
          rangeY = Math.random() * h

          mapInfo = _gameRegions.reduce((r, m) => {
            const { x: tx, y: ty } = m
            if (
              rangeX - tx > circleDMax / 2 &&
              tx + size - rangeX - circleDMax >= 0 &&
              ty - rangeY > circleDMax / 2 &&
              rangeY + size - ty - circleDMax >= 0
            ) {
              r = m
            }
            return r
          }, mapInfo)
        }

        return { rangeX, rangeY, ...mapInfo }
      }
    },
    createCircle(tooltipD3El) {
      const svg = d3.select('#game-v')

      const rangeCircle = this.getRandomCoordnates()
      const { mid } = rangeCircle
      // console.log('>>>>>>Game Ponit >>>>>>>>>>>', rangeCircle)
      const { rangeX, rangeY } = rangeCircle
      const rangeCirclePoint = svg
        // .select(`#${mid}`)
        // .selectAll('circle')
        // .data([rangeCircle])
        // .enter()
        .append('circle')
        .attr('cx', rangeX)
        .attr('cy', rangeY)
        .attr('r', circleD / 2)
        .attr('fill', '#FF00FF')
        .style('cursor', 'pointer')

      const rebuildTooltip = this.buildTooltipDiv.bind(this)

      rangeCirclePoint
        .on('mouseover', function (evt, d) {
          const { pageX, pageY } = evt

          let left = pageX + tipDiffW
          const isOverX = pageX + tipWidth + tipDiffW + tipPaddingX * 2 - w > 0
          if (isOverX) {
            left = pageX - tipWidth - tipPaddingX * 2 - tipDiffW
          }

          const isOverY = parseInt(pageY) + tipHightMax - h > 0
          let top = pageY + tipDiffH - 64
          if (isOverY) {
            top = pageY - tipDiffH - tipHightMax - tipPaddingY * 2
          }

          const _data = { ...rangeCircle, ...d, left, top }
          rebuildTooltip(tooltipD3El, _data)
        })
        .on('mouseout', function (evt, d) {
          tooltipD3El.transition().duration(500).style('opacity', 0)
        })

      // point click
      const gotoGameHandler = this.gotoGame.bind(this)
      rangeCirclePoint.on('click', function (evt, d) {
        let routeData = cloneDeep({ ...rangeCircle, ...d })

        gotoGameHandler(routeData)
      })

      // remove
      setTimeout(() => {
        rangeCirclePoint.remove()
        this.clearTooltipDiv(tooltipD3El)
      }, disappearSeconds * 1000)

      blink()

      // blink
      function blink() {
        rangeCirclePoint
          .attr('r', circleRMin)
          .attr('opacity', 1)
          .transition()
          .duration(shakeDuration * 1000)
          .attr('r', circleDMax / 2)
          .attr('opacity', 0)
          .on('end', blink)
      }
    },
    buildTooltipDiv(divEl, data = {}) {
      // const divEl = d3.select(`#${tool}`)

      // console.log('>>>>>>>>>>>>>>mouseover>>>>>>>>>>>>>>>>', divEl, data)
      divEl
        .transition()
        .duration(500)
        .style('background', '#ffffff')
        .style('opacity', 0.9)
      const { rangeX, rangeY, name, mid, type, left = 0, top = 200 } = data

      const btnId = `btn_${mid}_${new Date().getTime()}`
      const _html = `<h3>${name}</h3>
        <div class="game-info-line">
          <span class="game-label-id">Game ID:</span><span class="game-text-id">${mid}</span>
        </div>
        <div class="game-info-line">
          <span class="game-label-type">Game Type:</span><span class="game-text-type">${type}</span>
        </div>
        <div class="game-info-line">
          <span class="game-label-coor">X :</span><span class="game-text-coor">${rangeX}</span> 
        </div>
        <div class="game-info-line">
          <span class="game-label-coor">Y :</span><span class="game-text-coor">${rangeY}</span>
        </div>
        <div class="game-action"></div>
        `
      divEl
        .html(_html)
        // .style('position', 'absolute')
        // .style('float', 'left')
        // .style('font-size', '12px')
        // .style('padding', '6px')
        .style('left', `${left}px`)
        .style('top', `${top}px`)

      divEl
        .select('.game-action')
        .selectAll('button')
        .data([data])
        .enter()
        .append('button')
        .attr('id', btnId)
        .text('点击闪烁圆点进入游戏')
    },
    clearTooltipDiv(divEl) {
      if (divEl) {
        divEl.style('opacity', 0).html('')
      } else {
      }
    },
    gotoGame(data = {}) {
      // console.log('Go To Game:', data)
      this.$router.push({ name: 'game', params: data })
    }
  },

  mounted() {
    this.createSvgMap()
  },
  beforeDestroy() {
    if (this.timer) clearInterval(this.timer)
  }
}
</script>
<style lang="scss">
a {
  cursor: pointer;
}
</style>
